@keyframes slideInFromLeft100 {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes scaleIn {
    0% {
        transform: scale(1.3);
    }
    100% {
        transform: scale(1);
    }
}

.row {
    display: flex;
    overflow: hidden;
}

.image-grid--v4 {
    grid-column: 1/-1;
    .box {
        z-index: 10;
        position: absolute;
        width: 43%;
        transform: translate(10%, 50%);
        background: white;
        padding: 2rem 1rem;
        &.inView {
            animation: slideInFromLeft100 1.5s;
        }
        &:not(.inView) {
            opacity: 0;
        }
    }
    .box__content {
        position: relative;
        margin-left: 4.5rem;
        padding-bottom: 1rem;
        padding-right: 2rem;
        .box__pre-strap {
            text-transform: uppercase;
            display: flex;
            font-weight: 700;
            span {
                display: block;
                &.text {
                    color: $color-red50;
                    @include font-family('heading');
                    @include heading-font-features();
                }
                &.line {
                    flex: 1;
                    border-top: 1px solid $color-gray6;
                    margin: auto 0 auto 1rem;
                }
            }
        }
        .box__content__body {
            border-bottom: 1px solid $color-gray6;
            padding-bottom: 1rem;
            margin-bottom: 1rem;
        }
        .box__post-strap {
            .icon__wrapper {
                background-color: #d61726;
                border-radius: 50%;
                color: white;
                display: inline-flex;
                height: 2rem;
                margin-bottom: 0.5rem;
                width: 2rem;
                margin-right: 0.5rem;
                .icon {
                    margin: auto;
                    &.fa-instagram {
                        &:before {
                            @include gammatype();
                        }
                    }
                }
            }
            a {
                color: $color-red50;
                border-bottom-color: $color-red50;
                font-weight: 600;
                background-image: none;
                &:hover {
                    border-bottom-color: $color-red50;
                    background-image: none;
                }
            }
        }
    }
    .box__border {
        position: absolute;
        top: 0;
        bottom: 0;
        left: -1px; // To align with box's left border
        right: 0;
        background: transparent;
        width: 0;
        border-left: 15px solid transparent;
        border-image: url(https://www.city.ac.uk/__data/assets/file/0015/710241/community.svg)
            65 round;
        filter: invert(67%) sepia(1%) saturate(0%) hue-rotate(151deg)
            brightness(98%) contrast(85%);
    }

    .box__border--two {
        left: 0.75rem;
    }
}

.image-grid--v4 img {
    &.inView {
        animation: slideInFromLeft100 1s, scaleIn ease-out 3s;
        overflow: hidden;
        flex-basis: calc(100% / 5);
        object-fit: cover;
        max-width: calc(100% / 5);
    }
    &:not(.inView) {
        opacity: 0;
    }
}
